import { h, Component } from 'preact';

import {
	Circle,
	FeatureGroup,
	LayerGroup,
	LayersControl,
	Map,
	Marker,
	Popup,
	Rectangle,
	TileLayer,
	VideoOverlay,
} from 'react-leaflet'

const { BaseLayer, Overlay } = LayersControl

import '../../../node_modules/leaflet/dist/leaflet.css';

import L from 'leaflet';

delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
    iconRetinaUrl: require('../../../node_modules/leaflet/dist/images/marker-icon-2x.png'),
    iconUrl: require('../../../node_modules/leaflet/dist/images/marker-icon.png'),
    shadowUrl: require('../../../node_modules/leaflet/dist/images/marker-shadow.png')
});


const DEFAULT_VIEWPORT = {
	center: [52.051959,15.085487],
	zoom: 13,
}

const position = [52.051959,15.085487]
const center = position;
const rectangle = [
	[52.051959, 15.085487],
	[52.061959, 15.095487],
];

const circle2 = [52.061959, 15.085487]

import style from './style';

export default class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
		    viewport: DEFAULT_VIEWPORT,
		};
	}

	onClickReset = () => {
		this.setState({ viewport: DEFAULT_VIEWPORT })
	}

	onViewportChanged = (e) => {
		console.log('onViewportChanged', e);
	}

	render() {
		return (
			<div class={`${style.home}`} style='height: 100%;'>
				<Map center={position} zoom={15} style='width: 100%; min-height: 500px; height: 100%;' className='h-100'>
					<LayersControl position="topright">
						<BaseLayer checked name="OpenStreetMap.Mapnik">
							<TileLayer
								attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
								url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
							/>
						</BaseLayer>
						<BaseLayer name="OpenStreetMap.BlackAndWhite">
							<TileLayer
								attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
								url="https://tiles.wmflabs.org/bw-mapnik/{z}/{x}/{y}.png"
							/>
						</BaseLayer>
						<Overlay name="Marker with popup">
							<Marker position={center}>
								<Popup>
									A pretty CSS3 popup. <br /> Easily customizable.
								</Popup>
							</Marker>
						</Overlay>
						<Overlay checked name="Layer group with circles">
							<LayerGroup>
								<Circle center={center} fillColor="blue" radius={200} />
								<Circle
									center={center}
									fillColor="red"
									radius={100}
									stroke={false}
								/>
								<LayerGroup>
									<Circle
										center={circle2}
										color="green"
										fillColor="green"
										radius={100}
									/>
								</LayerGroup>
							</LayerGroup>
						</Overlay>
						<Overlay name="Feature group">
							<FeatureGroup color="purple">
								<Popup>Popup in FeatureGroup</Popup>
								<Circle center={circle2} radius={200} />
								<Rectangle bounds={rectangle} />
							</FeatureGroup>
						</Overlay>
					</LayersControl>
				</Map>
			</div>
		);
	}
}
